import React, { useState } from "react";
import "../styles/registration.css";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import VisibilityIcon from "@mui/icons-material/Visibility";
import Button from "@mui/material/Button";

const Registration = () => {
  let [showpass, setShowpass] = useState("password");
  let handleShowPass = () => {
    if (showpass === "password") {
      setShowpass("text");
    } else {
      setShowpass("password");
    }
  };

  return (
    <>
      <Container fixed>
        <Grid className="box" container spacing={2}>
          <Grid item xs={6}>
            <Box className="froms">
              <Box className="reg-header">
                <h2>Get started with easily register</h2>
                <p>Free register and you can enjoy it</p>
              </Box>
              <Box className="inputs">
                <Box className="input-box">
                  <TextField
                    type="text"
                    label="Full Name"
                    variant="outlined"
                    className="w-70"
                  />
                </Box>
                <Box className="input-box">
                  <TextField
                    type="email"
                    label="Email"
                    variant="outlined"
                    className="w-70"
                  />
                </Box>
                <Box className="input-box" sx={{ position: "relative" }}>
                  <TextField
                    type={showpass}
                    label="Password"
                    variant="outlined"
                    className="w-70"
                  />
                  <Box className="input-icon" onClick={handleShowPass}>
                    {showpass === "password" ? (
                      <VisibilityOffIcon />
                    ) : (
                      <VisibilityIcon />
                    )}
                  </Box>
                </Box>
                <Box className="input-box">
                  <TextField
                    type="password"
                    label="Confirm Password"
                    variant="outlined"
                    className="w-70"
                  />
                </Box>
                <Box className="signup-btn">
                  <Button variant="contained">Sign Up</Button>
                </Box>
              </Box>
            </Box>
            <Box className="si-link">
              <p>
                Already have an account ? <span>Sign In</span>
              </p>
            </Box>
          </Grid>
          <Grid item xs={6}>
            <picture>
              <img
                className="sugnup-img"
                loading="lazy"
                src="../img/signup.png"
                alt=""
              />
            </picture>
          </Grid>
        </Grid>
      </Container>
    </>
  );
};

export default Registration;
